import React, { Component, Fragment } from 'react';
import { BsFillPlusSquareFill } from "react-icons/bs";
import Ajout from './Ajout';
import Edit from './Edit';
import Liste from './Liste';

class Admin extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            articles:[
                {id:1, titre:'La politique du cannabis en France, une longue histoire ', date:'25 octobre 2020', auteur:'Clea Chakraverty', image:'1.jpg', lien:'https://theconversation.com/la-politique-du-cannabis-en-france-une-longue-histoire-145849',contenu:'Le président Emmanuel Macron et le gouvernement français ne sont pas favorables à la légalisation du cannabis « récréatif ». Cependant, la France semble de plus en plus envisager une éventuelle légalisation de l’usage thérapeutique du cannabis. Comment comprendre cette évolution, toute aussi sociétale que politique ? L’histoire coloniale du cannabis peut apporter quelques éclairages.' },
                {id:2, titre:'Faut-il légaliser le cannabis en France ? Vingt arguments pour se forger une opinion', date:'22 janvier 2021', auteur:'Anne-Aël Durand, Jérémie Baruch et Pierre Breteau', image:'2.jpg', lien:'https://www.lemonde.fr/les-decodeurs/article/2019/07/02/faut-il-legaliser-le-cannabis-vingt-arguments-pour-se-forger-une-opinion_5484277_4355770.html',contenu:'L’Uruguay en 2013, le Colorado et l’Etat de Washington en 2014, la Californie et le Canada en 2018… plusieurs pays ont fait récemment le choix de rendre légale la consommation de cannabis récréatif. En France, le débat ressurgit régulièrement. Il a jusque-là toujours été écarté d’un revers de main par les exécutifs qui se sont succédé.' },
                {id:3, titre:'Cannabis au volant, crash au tournant !', date:'27 juin 2014', auteur:'David Bême', image:'3.jpg', lien:'https://www.doctissimo.fr/html/dossiers/drogues/cannabis/9165-cannabis-accident-route.htm',contenu:"Moins dangereuse que la consommation d'alcool, la fumette doublerait tout de même les risques d'accidents mortels. Et l'association alcool et joint multiplierait ce danger par 14 ! Près de deux ans après la loi pénalisant les conducteurs-fumeurs, découvrez le réel impact du cannabis au volant." },
                {id:4, titre:'L’ONU classe le Maroc parmi les plus grands producteurs de cannabis dans le monde', date:'28 Juin 2018', auteur:'Rédaction ', image:'4.jpg', lien:'https://www.tsa-algerie.com/lonu-classe-le-maroc-parmi-les-plus-grands-producteurs-de-cannabis-dans-le-monde/',contenu:'Le Maroc a produit 35 652 tonnes d’herbe de cannabis et 713 tonnes de résine de cannabis durant l’année 2016, selon les données de l’Office des Nations unies contre la drogue et le crime (ONUDC) publiées dans son nouveau rapport mondial sur les drogues.' },
            ],
            editForm: false,
            indexMod: -1,
            editarticle: {id:-1, titre:'',date:'',auteur:'',image:'',lien:'',contenue:''},
         }
    }

    componentDidMount = ()=>{
    
        let articles1 = JSON.parse(localStorage.getItem('articlesKey'));
        if(!articles1 || articles1.length === 0){
            localStorage.setItem('articlesKey',JSON.stringify(this.state.articles));
            let articlesrecup = JSON.parse(localStorage.getItem('articlesKey'));
            this.setState({articles: articlesrecup});
        }else{

            let articles2 = JSON.parse(localStorage.getItem('articlesKey'));
            this.setState({articles: articles2});
        }
       
    }

    handleRemove = (index)=>{
        console.log(index);

        let newArticles = [...this.state.articles];
        let arts = newArticles.filter((v, id) =>{
            return id !== index;
        });
        

        this.setState({articles:arts},()=>{
            localStorage.setItem('articlesKey', JSON.stringify(this.state.articles));
            console.log(this.state);
        });

    }

    handleShow = (etat)=>{
        this.setState({editForm:true});
    }

    handleArct = (art)=>{
        art.id = this.state.articles[this.state.articles.length -1].id + 1;
        let articleClone = [...this.state.articles, art];
        this.setState({articles: articleClone}, ()=>{
            localStorage.setItem('articlesKey', JSON.stringify(this.state.articles));
        });
    }
    handleItem = (index)=>{
        const AEdit =  this.state.articles[index];
        this.setState({editarticle: AEdit, indexMod: index});
    }

    handleUpdate = (data)=>{
        let articleUp = [...this.state.articles];
        articleUp[this.state.indexMod] = data;
        this.setState({articles: articleUp}, ()=>{
            localStorage.setItem('articlesKey', JSON.stringify(this.state.articles));
        });
    }
    render() { 
        return ( 
            <Fragment> 
                <h1 className="bg-secondary text-white text-center">Administration</h1>
                {
                    (!this.state.editForm && this.state.indexMod === -1) 
                    &&  <button onClick={this.handleShow} className="btn btn-warning offset-11 mb-1 ">
                        <BsFillPlusSquareFill />
                       </button>
                }
               
                {
                    (!this.state.editForm && this.state.indexMod > -1)
                    ?<Edit artShared = {this.state.editarticle} update = {this.handleUpdate}/>
                    :
                    (this.state.editForm)
                    ?<Ajout getArticle = {this.handleArct} />
                    :<Liste arcts = {this.state.articles} handleDelete = {this.handleRemove} handleEdit = {this.handleItem} />
                }
                
               
                
            </Fragment> 
         );
    }
}
 
export default Admin;