import React, { Component, Fragment } from 'react';
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            login:'',
            pass:'',
            loginErreur:'',
            passErreur:'',
            errorMessage:''
        }
    }
    handleChange =(e)=>{
        const {name, value}=e.target;
        let val = value;
        let err1 = '', err2 ='';
        if (name === "login"){
            if(val === ''){
                err1 = <strong>Le champ login est requis</strong>
            } 
        }else if(name ==="pass"){
            if(val === ''){
                err2 = <strong>Le champ mot de passe est requis</strong>
            }
        }
        this.setState({loginErreur:err1, passErreur:err2})
        this.setState({[name]:val})
    }
    handleSubmit=(e)=>{
        e.preventDefault();
         const{login,pass} = this.state
         if(login !== '' && pass !== ''){
             if(login === "Dupont" && pass ==="123"){
                 const d = new Date();
                 const cle = d.toLocaleDateString();
                 sessionStorage.setItem('token',cle);
                 this.props.history.push('/admin123')
                 window.location.reload();
             } else {
                 const fail = <div className="alert alert-warning">Mauvais login et/ou mot de passe</div>
                 this.setState({errorMessage:fail})
             }
         } else {
             const error = <div className="alert alert-danger">Veuillez saisire les deux champs</div>
             this.setState({errorMessage:error})
         }
    }
    render() { 
        return (
            <Fragment>
                <div>
                    <div className="container">
                        <p className="text-center" id="test">Ceci est la connection a la partie admin si vous êtes ici sans y avoir accès veuillez nous contacter</p>
                    </div>
                    <div className="card w-100">
                        {this.state.errorMessage}
                        <div className="card-header">Formulaire de connexion</div>
                        <div className="card-body">
                            <form onSubmit={this.handleSubmit}>
                                <div className="mb-1">
                                    <label htmlFor="login" className="form-label">Login</label>
                                    <input type="text" onChange={this.handleChange} className="form-control" name="login" id="login" placeholder="Entrez vos identifiant..." />
                                </div>
                                {this.state.loginErreur}
                                <div className="mb-1">
                                    <label htmlFor="pass" className="form-label">Mot de passe</label>
                                    <input type="password" onChange={this.handleChange} className="form-control" name="pass" id="pass" />
                                </div>
                                {this.state.passErreur}
                                <button type="submit" className="btn btn-primary col-12">Soumettre</button>
                            </form>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}
 
export default Login;