import React, { Fragment } from 'react'
import { Scrollbars } from 'rc-scrollbars';


function Acceuil() {
    const articles = JSON.parse(localStorage.getItem('articlesKey'));
    return (
        <Fragment>
            <div className="container">
                <div className="bg-light p-5">
                    <div className="container text-center" >
                        <h1 className="display-4" >Information sur la légalisation du cannabis en France et dans le Monde</h1>
                        <p className="lead">Toute l'info sur le cannabis</p>
                    </div>
                </div>
                <div className="row row-cols-1 row-cols-md-2 g-4">
                    {articles.map((article, index) => {
                        return <div className="col" key={index}>
                            <div className="card" style={{backgroundColor:'#39b26a'}}>
                                <img src={`images/${article.image}`} className="card-img-top" alt="..." style={{ width: 500, height: 400 }} />
                                <div className="card-body">
                                    <h5 className="card-title text-center strong">Article N°{article.id}</h5>
                                    <ul className="list-group list-group-flush">
                                        <li className="list-group-item"><strong>Titre :</strong> {article.titre}</li>
                                        <li className="list-group-item"><strong>Contenu : </strong><Scrollbars style={{ width: 500, height: 100 }}> {article.contenu}</Scrollbars></li>
                                        <li className="list-group-item"><strong>Auteur : </strong> {article.auteur}</li>
                                        <li className="list-group-item"><strong>Date : </strong> {article.date}</li>
                                        <li className="list-group-item"><button className="btn btn-success" data-bs-toggle="modal" data-bs-target={`#num${index}`}>Plus d'info</button></li>
                                    </ul>
                                </div>
                            </div>

                            <div className="modal fade" id={`num${index}`} aria-labelledby="exampleModalLabel" aria-hidden="true">
                                <div className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header">
                                            <h5 className="modal-title" id="exampleModalLabel">Info complémentaire</h5>
                                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                        </div>
                                        <div className="modal-body">
                                            <div className="card">
                                                <img src={`images/${article.image}`} width="150" alt="..." />
                                                <div className="col-md-7">
                                                    <div className="card-body">
                                                        <h5 className="card-title">{article.titre}</h5>
                                                        <p class="card-text"><a href={article.lien}>Lien de l'article</a></p>
                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                        <div className="modal-footer">
                                            <button type="button" className="btn btn-primary">Confirmer</button>
                                            <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Annuler</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    })}
                </div>

            </div>
        </Fragment>
    )
}

export default Acceuil




