import React, { Fragment } from 'react';
import {FaEdit, FaTrashAlt } from "react-icons/fa";
import { ToastContainer, toast } from 'react-toastify';
import { Scrollbars } from 'rc-scrollbars';
const Liste = (props) => {

    return ( 
         <Fragment>
            <div className="container">
                <table className="table table-striped table-hover text-center">
                    <thead className="table-dark">
                        <tr>
                            <th>Id</th><th>Titre</th><th>Date</th><th>Auteur</th><th>Image</th><th>Lien</th><th>Contenu</th><th colSpan="2">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.arcts.map((arct, index) => {
                            return (
                                <tr key={index}>
                                    <td>{arct.id}</td>
                                    <td>{arct.titre}</td>
                                    <td>{arct.date}</td>
                                    <td>{arct.auteur}</td>
                                    <td><img src={`images/${arct.image}`} alt="" width="100" /></td>
                                    <td><Scrollbars style={{ width: 300, height: 100 }}>{arct.lien}</Scrollbars></td>
                                    <td>
                                        <Scrollbars style={{ width: 300, height: 100 }}>
                                            {arct.contenu}
                                        </Scrollbars>
                                    </td>
                                    <td>
                                        <button onClick={() => { props.handleEdit(index) }} className="btn btn-success"><FaEdit /></button>
                                    </td>
                                    <td>
                                        <button onClick={() => {
                                            if (window.confirm('Etes vous sûr de supprimer cette article?')) {
                                                props.handleDelete(index)
                                                toast('article supprimé avec succès', { className: 'bg-info text-white fw-bold' })
                                            }}}className="btn btn-danger"><FaTrashAlt /></button>
                                    </td>
                                </tr>
                            )
                        })}
                    </tbody>
                </table>
            </div>
        <ToastContainer/>
        </Fragment>
     );
}
 
export default Liste;