import React, { Component, Fragment } from 'react';
import { FaRegHandPointLeft } from 'react-icons/fa';
import { Link } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

class Edit extends Component {
    constructor(props) {
        super(props);
        
        this.initState = { 
            titre:"",
            date:"",
            auteur:"",
            image:"",
            lien:0,
            contenu:''
           
         }

         this.state = this.initState;
    }

    componentDidMount = ()=>{
        this.setState(this.props.artShared);
        console.log(this.props.artShared);
    }
    handleChange = (e)=>{
       
        const {name, type, checked, value} = e.target;
        const val = type === 'checkbox' ? checked : value;
        this.setState({[name]:val});

    }
    handleSubmit = (e)=>{
        e.preventDefault();
        let img = '';
        if(this.state.image){

            img = this.state.image.match(/[a-zA-Z0-9-_]+\.(jpg|png)/)[0];
        }
        const newArt = {
            titre: this.state.titre,
            date: this.state.date,
            auteur: this.state.auteur,
            lien: this.state.lien,
            image: img,
            contenu: this.state.contenu
        }
        this.props.update(newArt);

        this.setState(this.initState);
        toast.info('Article modifier avec succès!');

    }
    render() {
        const {titre, date, auteur, lien, contenu} = this.state;
       
        return (
            <Fragment>
                <div className="row">
                    <div className="col-6 offset-3">
                        <div className="card">
                            <div className="card-header text-center"> Formulaire d'édition</div>
                            <div className="card-body">
                                <form className="row g-3 needs-validation" onSubmit={this.handleSubmit}>
                                    <div className="col-md-6">
                                        <label htmlFor="titre" className="form-label">Titre</label>
                                        <input type="text" required className="form-control" id="titre" name="titre" value={titre} onChange={this.handleChange} placeholder="Entrer la titre" />

                                    </div>
                                    <div className="col-md-6">
                                        <label htmlFor="date" className="form-label">Date</label>
                                        <input type="text" required className="form-control" id="date" name="date" value={date} onChange={this.handleChange} placeholder="Entrer la date de publication" />
                                    </div>
                                    <div className="col-md-6">
                                        <label htmlFor="auteur" className="form-label">Auteur</label>
                                        <input type="text" required className="form-control" id="auteur" name="auteur" value={auteur} onChange={this.handleChange} placeholder="Entrer l'auteur" />

                                    </div>
                                    <div className="col-md-6">
                                        <label htmlFor="lien" className="form-label">Lien</label>
                                        <input type="url" required className="form-control" id="lien" value={lien} onChange={this.handleChange} name="lien" placeholder="Entrer le lien" />
                                    </div>
                                    <div className="col-md-12">
                                        <label htmlFor="image" className="form-label">Image</label>
                                        <input type="file" className="form-control" id="image" onChange={this.handleChange} name="image" />
                                    </div>
                                    <div className="col-md-12">
                                        <label htmlFor="contenu" className="form-label">Contenu</label>
                                        <input type="text" required className="form-control" id="contenu" value={contenu} onChange={this.handleChange} name="contenu" placeholder="Entrer le contenu" />
                                    </div>
                                    <div className="col-12">
                                        <button className="btn btn-primary col-12" type="submit">Soumettre</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <Link to='/admin123/reload'> <FaRegHandPointLeft size="25" /> </Link>
                    </div>
                </div>
                <ToastContainer />
            </Fragment>
        );
    }
}
 
export default Edit;